body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }
.App {
    max-width: 1200px;
    margin: 60px auto;
  }
  
  .title {
    display: flex;
    justify-content: center;
  }
  
  .canvas-presets {
    width: 750px;
    display:flex;
    justify-content: center;
  }
  
  .canvas {
    border: 10px solid black;
    width: 1000px;
    height: 1000px;
    display:flex;
    flex-wrap: wrap;
    outline: 1px solid black;
    outline-offset: -11px;
    margin: 0 0 0 450px;
  }
  
  .buttons-body {
    display: flex; 
    justify-content: space-around;
    width: 500px;
  }
  
  .presets-body {
    width: 200px;
  }
  
  .presets {
    margin: 10px 0 0 10px;
  }
  
  .preset-group1 {
    display:flex;
  }
  
  .preset-group2 {
    display:flex;
  }
  
  .preset-group3 {
    display:flex;
  }
  
  .preset-group4 {
    display:flex;
  }
  
  .random-body {
    margin: 10px 0 0 10px;
  }
  
  .generation-body {
    margin: 10px 0 0 10px;
  }
  
  .speed-body {
    margin: 10px 0 0 10px;
  }
  
  .speedtext-body {
    margin: 10px 0 0 10px;
  }
